* {
	box-sizing: border-box;
	margin: 0;
	border: 0;
}

html, body {
  background-color: #da4167;
  width: 100%;
  height: 100%;
}

.close-button {
	-webkit-animation: moveToLeft .6s ease both;
	animation: moveToLeft .6s ease both;
}

@keyframes border {
  from {border: none;}
  to {border: 2px solid #083d77;}
}

@-webkit-keyframes moveFromLeft {
	from { -webkit-transform: translateX(-100%); }
}
@keyframes moveFromLeft {
	from { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}

@-webkit-keyframes moveFromRight {
	from { -webkit-transform: translateX(100%); }
}
@keyframes moveFromRight {
	from { -webkit-transform: translateX(100%); transform: translateX(100%); }
}

@-webkit-keyframes moveToLeft {
	from { }
	to { -webkit-transform: translateX(-100%); }
}
@keyframes moveToLeft {
	from { }
	to { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}


/*APP PAGE*/

.app-main {
	position: absolute;
	width: 100%;
	height: 100%;
}

.link {
	text-decoration: none;
	font-family: "PT Sans", sans-serif;
	font-size: 23px;
	color: #083d77;
}

#projects-main {
	background-color: #ebebd3;
	z-index: 1;
    border: 2px solid #083d77;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 40%;
    text-decoration: none;
    display: flex;
    position: absolute;
    top: 0;
    bottom: auto;
    left: 0;
    right: auto;
   	transition: background-color 1s, width 1s, height 1s, transform 1s, z-index 1s, border 1s;
    overflow: hidden;
}

#projects-main:hover {
	background-color: #da4167;
	width: 70%;
	height: 50%;
	z-index: 2;
	transform: scale(1);
}

body:has(#projects-main:hover) #about {
	height: 50%;
}

body:has(#projects-main:hover) #contacts-main {
	width: 30%;
}

body:has(#projects-main:hover) #logo {
	width: 20%;
    height: 20%;
    transform: translate3d(50%, 50%, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    z-index: 10;
    animation-name: border;
    animation-iteration-count: 1;
    animation-delay: 2s;
}

body:has(#projects-main:hover) #logo ~ #resume-main {
	height: 30%;
	width: 50%;
}

body:has(#projects-main:hover) #logo ~ #contacts-main {
	height: 70%;
}


body:has(#projects-main:hover) #logo ~ #about {
	width: 50%;
}

#projects-main:hover .link {
	color:#f4d35e;
	padding: 15px;
	border: 4px solid #f4d35e;
}


#contacts-main {
